.aside {
  &__block {
    margin-bottom: 35px;
    @include lgmin {
      @include shadow; }
    @include sm {
      margin-bottom: 15px; } }
  &__btn {
    @include lg {
      text-align: center; } } }

.aside-nav {
  @include lg {
    text-align: center; }
  &__title {
    background-color: $accent-2;
    color: $white;
    padding: 20px 42px;
    font-size: 44px;
    font-weight: 700;
    @include xxl {
      padding: 15px 30px; }
    @include lg {
      background-color: transparent;
      padding: 0;
      color: inherit;
      font-size: 36px;
 }      // margin-bottom: 30px
    @include md {
      font-size: 28px; }
    @include sm {
      font-size: 20px;
 } }      // margin-bottom: 15px
  &__nav {
    padding-left: 85px;
    padding-right: 40px;
    font-size: 36px;
    @include not-rtl {
      padding-left: 40px;
      padding-right: 85px; }
    @include xxl {
      padding-left: 65px;
      padding-right: 30px;
      @include not-rtl {
        padding-right: 65px;
        padding-left: 30px; } }
    @include xl {
      font-size: 30px; }
    @include lg {
      padding-left: 0 !important;
      padding-right: 0 !important;
      max-width: percentage(180 / 275);
      margin-left: auto;
      margin-right: auto;
      font-size: 26px;
      @include r(374) {
        max-width: 180px; } }
    @include md {
      font-size: 21px; }
    @include sm {
      font-size: 17px; }
    li {
      padding-top: 29px;
      padding-bottom: 29px;
      @include notlast {
        border-bottom: 2px solid $gray; }
      @include xxl {
        padding-top: 20px;
        padding-bottom: 20px; }
      @include xl {
        padding-top: 18px;
        padding-bottom: 18px; }
      @include md {
        padding-top: 15px;
        padding-bottom: 15px; }
      @include sm {
        padding-top: 12px;
        padding-bottom: 12px; } }
    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } } } }
