.services {
  $this: &;
  $grid-gutter: 10px;

  @include flex(space-between, normal, row, wrap);
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  @include sm {
    display: block; }
  &__item {
    font-size: em(24);
    width: 176px;
    margin-bottom: 17px;
    @include md {
      width: 160px; }
    @include sm {
      margin-left: auto;
      margin-right: auto; }
    @include xxs {
 } }      // max-width: 185px
  &:not(#{$this}--full) {
    @include lg {
      justify-content: center; }
    #{$this}__item {
      @include xl {
        width: 160px; }
      @include lg {
        width: 33.33333%;
        padding-left: $grid-gutter / 2;
        padding-right: $grid-gutter / 2; }
      @include md {
        width: 100%;
        max-width: percentage(185 / 375); }
      @include xs {
        max-width: 195px; } } }

  &--full {
    $grid-gutter-sm: 18px;
    @include sm {
      display: flex;
      margin-left: -30px - ($grid-gutter-sm / 2);
      margin-right: -30px - ($grid-gutter-sm / 2); }
    #{$this}__item {
      @include sm {
        margin-left: 0;
        margin-right: 0;
        padding-left: $grid-gutter-sm / 2;
        padding-right: $grid-gutter-sm / 2;
        width: 50%; } } } }

.service-card {
  $this: &;
  padding: 39px 23px 23px;
  background-color: $white;
  @include flex(flex-start, normal, column);
  @include shadow;
  height: calc(100% - 16px);
  margin-bottom: 16px;
  text-align: center;
  &__img {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px; }
  @include sm {
    flex-direction: row;
    align-items: center;
    text-align: inherit;
    padding: 9px;
    @include not-rtl {
      flex-direction: row-reverse; }
    #{$this}__img {
      margin-bottom: 0;
      margin-right: 0;
      margin-left: 18px; } } }

.service-img {
  $this: &;
  width: 79px;
  padding-bottom: 50px;
  border-bottom: 2px solid  $blue;
  @include sm {
    &:not(#{$this}--side) {
      width: 69px;
      flex: 0 0 69px;
      padding-bottom: 0;
      padding-left: 18px;
      border-bottom: 0;
      border-left: 2px solid  $blue;
      @include flex(center, center); } }
  @include r(374) {
    &:not(#{$this}--side) {
      flex: 0 0 50px; } }

  &--side {
    @include lg {
      width: 60px;
      padding-bottom: 0;
      border-bottom: 0; }
    @include md {
      width: 50px; }
    @include sm {
      width: 37px; } } }

