.social {
  li {
    display: inline-block;
    vertical-align: middle;
    @include notlast {
      margin-left: 62px;
      @include xl {
        margin-left: 55px; }
      @include lg {
        margin-left: 50px; }
      @include md {
        margin-left: 40px; }
      @include sm {
        margin-left: 30px; }
      @include xs {
        margin-left: 20px; }
      @include xxs {
        margin-left: 10px; }
      @include r(374) {
        margin-left: 5px; }
      @include not-rtl {
        margin-right: 62px;
        margin-left: 0;
        @include xl {
          margin-right: 55px;
          margin-left: 0; }
        @include lg {
          margin-right: 50px;
          margin-left: 0; }
        @include md {
          margin-right: 40px;
          margin-left: 0; }
        @include sm {
          margin-right: 30px;
          margin-left: 0; }
        @include xs {
          margin-right: 20px;
          margin-left: 0; }
        @include xxs {
          margin-right: 10px;
          margin-left: 0; }
        @include r(374) {
          margin-right: 5px;
          margin-left: 0; } } } }
  a {
    display: inline-block;
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    .icon {
      font-size: 58px;
      @include xl {
        font-size: 50px; }
      @include lg {
        font-size: 40px; }
      @include md {
        font-size: 30px; }
      @include sm {
        font-size: 22px; }
      @include xxs {
        font-size: 16px; } }
    .icon-linkedin {
      font-size: 70px;
      @include xl {
        font-size: 60px; }
      @include lg {
        font-size: 50px; }
      @include md {
        font-size: 40px; }
      @include sm {
        font-size: 32px; }
      @include xxs {
        font-size: 26px; } } } }
