.hero {
  $this: &;
  @include cover;
  background-color: $c-light;
  text-align: center;
  position: relative;
  &:not(#{$this}--lg) {
    min-height: 259px;
    padding-top: 15px;
    padding-bottom: 15px;
    @include xl {
      min-height: 180px; }
    @include lg {
      min-height: 110px; }
    @include md {
      min-height: 90px; }
    @include sm {
      min-height: 73px; } }
  &__video {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; }
  &__inner {
    position: relative;
    z-index: 1;
    @include flex(center, normal, column);
    padding-top: 15px;
    padding-bottom: 15px;
    // padding-top: 30px
    // padding-bottom: 30px
    // padding-top: 300px
    // padding-bottom: 148px
    // +xl
    //   padding-top: 250px
    //   padding-bottom: 110px
    // +lg
    //   padding-top: 200px
    //   padding-bottom: 80px
    // +md
    //   padding-top: 150px
    //   padding-bottom: 60px
    // +sm
    //   padding-top: 93px
 }    //   padding-bottom: 51px
  &__title {
    margin-bottom: 118px;
    @include xl {
      margin-bottom: 90px; }
    @include lg {
      margin-bottom: 70px; }
    @include md {
      margin-bottom: 55px; }
    @include sm {
      margin-bottom: 43px; } }

  &--lg {
    #{$this}__inner {
      min-height: $hero-height;
      @include xl {
        min-height: $hero-height-xl; }
      @include lg {
        min-height: $hero-height-lg; }
      @include md {
        min-height: $hero-height-md; }
      @include sm {
        min-height: $hero-height-sm; } } } }

