.s-services {
  padding-top: 115px;
  padding-bottom: 97px;
  @include xl {
    padding-top: 140px; }
  @include lg {
    padding-top: 80px;
    padding-bottom: 80px; }
  @include md {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include sm {
    padding-top: 41px;
    padding-bottom: 41px; } }

.s-form {
  padding-top: 175px;
  padding-bottom: 175px;
  @include xl {
    padding-top: 140px;
    padding-bottom: 140px; }
  @include lg {
    padding-top: 80px;
    padding-bottom: 120px; }
  @include md {
    padding-top: 60px; }
  @include sm {
    padding-top: 47px; }
  &__form {
    max-width: 986px;
    margin-left: auto;
    margin-right: auto;
    @include lg {
      max-width: percentage(287 / 375); }
    @include sm {
      max-width: none;
      width: 287px; }
    @include r(374) {
      width: 100%; } } }

.s-form-service, .s-descr {
  background-color: #c8e6f3;
  @include md {
    background-image: url('../img/bottom-left.png'), url('../img/top-right.png') !important;
    background-position: rem(30) 100%, 100% 0;
    background-size: 85px 80px, 120px 70px; } }


.s-descr {
  padding-top: 148px;
  padding-bottom: 220px;
  position: relative;
  .container {
    position: relative;
    z-index: 1; }
  @include xl {
    padding-top: 120px;
    padding-bottom: 170px; }
  @include lg {
    padding-top: 100px;
 }    // padding-bottom: 140px
  @include md {
    padding-top: 70px;
    padding-bottom: 120px; }
  @include sm {
    padding-top: 42px;
    padding-bottom: 60px; }
  &__title {
    margin-bottom: 62px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 16px; } }
  &__img {
    position: absolute;
    z-index: 0;
    @include mdmin {
      display: none; }
    &--top-right {
      top: 0;
      right: 0; }
    &--bottom-left {
      bottom: 0;
      left: rem(30); } } }

.s-grid-items {
  position: relative;
  z-index: 2;
  padding-top: 297px;
  padding-bottom: 65px;
  @include xl {
    padding-top: 250px;
    padding-bottom: 50px; }
  @include lg {
    padding-top: 200px;
    padding-bottom: 40px; }
  @include md {
    padding-top: 150px;
    padding-bottom: 30px; }
  @include sm {
    padding-top: 94px;
    padding-bottom: 23px; }
  &__img {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    width: percentage(705 / 1920);
    text-align: center;
    @include sm {
      width: 207px; } }
  &__title {
    @include vcenter;
    z-index: 1; } }

.s-form-service {
  padding-top: 60px;
  padding-bottom: 46px;
  @include xl {
    padding-top: 50px;
    padding-bottom: 45px; }
  @include lg {
    padding-top: 45px;
    padding-bottom: 45px; }
  @include sm {
    padding-top: 38px;
    padding-bottom: 38px;
    overflow: hidden; }
  &__form {
    margin-bottom: 99px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 45px; }
    @include sm {
      margin-bottom: 37px; } } }

.s-blog {
  padding-top: 130px;
  padding-bottom: 130px;
  @include xl {
    padding-top: 100px;
    padding-bottom: 100px; }
  @include lg {
    padding-top: 70px;
    padding-bottom: 70px; }
  @include md {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include sm {
    padding-top: 36px;
    padding-bottom: 33px; } }

