.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  @include before {
    z-index: 0;
    @include coverdiv;
    background-color: $white;
    @include shadow;
    @include lg {
      z-index: 2; } }
  &__logo {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    @include xl {
      width: 150px; }
    @include lg {
      width: 80px; }
    .lazy {
      &--loaded {
        margin-top: -.9rem;
        @include xl {
          margin-top: -.5rem; }
        @include lg {
          margin-top: -.3rem; } } } }
  &__inner {
    @include flex(flex-start, center);
    height: $header-height;
    position: relative;
    @include lg {
      height: $header-height-lg; } }
  &__nav {
    margin-left: auto;
    margin-right: auto;
    max-width: 865px;
    @include lg {
      padding-left: 0;
      padding-right: 0; } }
  &__nav-wrap {
    width: 100%;
    @include lg {
      min-width: percentage(117 / 375);
      width: auto;
      position: fixed;
      z-index: 1;
      top: 0;
      right: 0;
      padding-top: $header-height-lg + 19px;
      padding-left: 10px;
      padding-right: 15px;
      padding-bottom: 10px;
      overflow: auto;
      background-color: $white;
      transform: translate(100%, 0);
      min-height: $hero-height-lg + $header-height-lg;
      @include shadow;
      @include ready {
        @include tr(transform .5s $easeOutQuart); }
      &--open {
        transform: translate(0, 0); } }
    @include md {
      min-height: $hero-height-md + $header-height-lg; }
    @include sm {
      min-height: $hero-height-sm + $header-height-lg; } }
  &__lang {
    // position: absolute
    // z-index: 1
    // left: 100%
    // top: 50%
    // transform: translate(0, -50%)
    // font-weight: 700
    // +tr(color .3s)
    // +hover
    //// color: $accent
    @include lg {
      // position: static
 }      // transform: translate(0, 0)
    display: none;
    @include lg {
      display: block; }
    &--desc {
      position: absolute;
      z-index: 3;
      right: 33px;
      top: 50%;
      transform: translate(0, -50%);
      display: block;
      @include lg {
        display: none; } } }
  &__burger {
    position: relative;
    z-index: 3;
    margin-left: auto;
    @include lgmin {
      display: none; } } }

.burger {
  display: block;
  width: 16px;
  height: 13px;
  position: relative;
  span {
    display: block;
    &, &::after, &::before {
      width: 100%;
      height: 1px;
      background-color: currentColor;
      backface-visibility: hidden;
      @include tr(transform .3s); }
    &::after, &::before {
      content: "";
      position: absolute;
      left: 0; }
    &::after {
      top: 0; }
    &::before {
      bottom: 0; } }
