.btn {
  display: inline-block;
  border: 1px solid $accent;
  background-color: $accent;
  padding: .45em 3.7em;
  font-weight: 700;
  font-size: em(24);
  text-align: center;
  @include tr(background-color .3s, color .3s, opacity .3s, border-color .3s);
  @include hover {
    background-color: $accent-2;
    border-color: $accent-2;
    color: $white; }
  @include sm {
    font-size: 16px; }
  &--lg {
    font-size: 44px;
    @include xxl {
      font-size: 38px; }
    @include xl {
      font-size: 32px; }
    @include lg {
      font-size: 26px; }
    @include md {
      font-size: 20px; } }
  &--full-lg {
    @include lgmin {
      width: 100%; } } }
