.post {
  $this: &;
  @include flex(flex-start, normal, column);
  background-color: $white;
  padding: 15px 43px 0;
  @include shadow;
  @include xl {
    padding: 15px 30px 0; }
  @include lg {
    padding: 15px 25px 0; }
  @include md {
    padding: 15px 20px 0; }
  @include sm {
    padding: 12px 12px 0; }
  &__title {
    margin-bottom: 25px;
    color: $c-text; }
  &__meta {
    margin-bottom: 15px; }
  &__img {
    @include cover;
    background-color: $gray;
    padding-top: percentage(482 / 972);
    margin-bottom: 61px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 12px; } }
  &__text {
    margin-bottom: 19px;
    flex-grow: 1; }
  &__link {
    display: block;
    padding: 25px;
    font-weight: 700;
    font-size: 34px;
    text-align: center;
    border-top: 1px solid  $c-light;
    margin-left: -43px;
    margin-right: -43px;
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    @include xl {
      padding: 20px;
      font-size: 28px;
      margin-left: -30px;
      margin-right: -30px; }
    @include lg {
      margin-left: -25px;
      margin-right: -25px; }
    @include md {
      padding: 17px;
      font-size: 20px;
      margin-left: -20px;
      margin-right: -20px; }
    @include sm {
      padding: 12px;
      font-size: 16px;
      margin-left: -12px;
      margin-right: -12px; } }


  &--sm {
    padding-left: 36px;
    padding-right: 36px;
    #{$this}__link {
      margin-left: -36px;
      margin-right: -36px;
      @include xl {
        margin-left: -30px;
        margin-right: -30px; }
      @include lg {
        margin-left: -25px;
        margin-right: -25px; }
      @include md {
        margin-left: -20px;
        margin-right: -20px; }
      @include sm {
        margin-left: -12px;
        margin-right: -12px; } }
    #{$this}__text {
      overflow: hidden;
      max-height: 4em; }
    #{$this}__img {
      padding-top: percentage(417 / 443);
      margin-bottom: 39px;
      @include xl {
        margin-bottom: 35px; }
      @include lg {
        margin-bottom: 30px;
 }        // padding-top: percentage(130 / 287)
      @include sm {
        padding-top: percentage(130 / 287); } } } }

