.nav {
  font-size: em(22);
  ul {
    @include flex(space-between, center);
    width: 100%;
    @include lg {
      display: block; } }
  li {
    @include lg {
      margin-bottom: 19px; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &__item-current {
    color: $accent; } }
